:where(html) {
  box-sizing: border-box;
  font-size: 1em;
  line-height: normal;
  -webkit-text-size-adjust: 100%;
}

*, *::before, *::after {
  box-sizing: inherit;
}

:where(body) {
  overflow-x: hidden;
  min-height: 100%;
  margin: 0;
}

:where(h1) {
  font-size: 2em;
  margin-block-end: 0.67em;
  margin-block-start: 0.67em;
}

/* Grouping content
   ========================================================================== */
:where(dl, ol, ul) :where(dl, ol, ul) {
  margin-block-end: 0;
  margin-block-start: 0;
}

:where(hr) {
  box-sizing: content-box;
  height: 0;
  color: inherit;
  overflow: hidden;
}

:where(pre, code, kbd, samp) {
  font-family: monospace, monospace;
  font-size: 1em;
}

/* Text-level semantics
   ========================================================================== */
:where(abbr[title]) {
  text-decoration: underline;
  text-decoration: underline dotted;
  text-decoration-style: dotted;
}

:where(b, strong) {
  font-weight: bolder;
}

:where(small) {
  font-size: 80%;
}

:where(sub, sup) {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

:where(sub) {
  bottom: -0.25em;
}

:where(sup) {
  top: -0.5em;
}

/* Tabular data
   ========================================================================== */
:where(table) {
  border-color: currentColor;
  text-indent: 0;
}

/* Forms
   ========================================================================== */
:where(button, input, optgroup, select, textarea) {
  max-width: 100%;
  font-size: 100%;
  line-height: inherit;
  margin: 0;
}

:where(button) {
  overflow: visible;
}

:where(button, select) {
  text-transform: none;
}

:where(select) {
  word-wrap: normal;
}

:where(input, textarea):disabled {
  color: #404040;
  opacity: 0.8;
}

:where(button, input:is([type=button], [type=color], [type=reset], [type=submit]))::-moz-focus-inner {
  border-style: none;
  padding: 0;
}
:where(button, input:is([type=button], [type=color], [type=reset], [type=submit]))::-moz-focusring {
  outline: 1px dotted ButtonText;
}

:where(:-moz-ui-invalid) {
  box-shadow: none;
}

:where(button, input:is([type=button], [type=reset], [type=submit])) {
  display: inline-block;
  -moz-appearance: button;
  -webkit-appearance: button;
  padding: 0.125em 0.375em;
  text-decoration: none;
  cursor: pointer;
}
:where(button, input:is([type=button], [type=reset], [type=submit])):hover, :where(button, input:is([type=button], [type=reset], [type=submit])):focus, :where(button, input:is([type=button], [type=reset], [type=submit])):active {
  text-decoration: none;
}

:where(button,
input:is([type=button], [type=reset], [type=submit]),
input[type=file]::file-selector-button,
optgroup,
select) {
  color: inherit;
}
:where(button,
input:is([type=button], [type=reset], [type=submit]),
input[type=file]::file-selector-button,
optgroup,
select):disabled {
  color: #808080;
  cursor: default;
  opacity: 1;
}

:where(fieldset) {
  padding: 0.35em 0.75em 0.625em;
}

:where(legend) {
  box-sizing: border-box;
  display: table;
  max-width: 100%;
  white-space: normal;
  padding: 0;
}

:where(progress) {
  vertical-align: baseline;
}

::-webkit-inner-spin-button,
::-webkit-outer-spin-button {
  height: auto;
}

::-webkit-input-placeholder {
  color: inherit;
  opacity: 0.54;
}

:where(input[type=search]) {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

/* Interactive
   ========================================================================== */
:where(details) {
  display: block;
}

:where(details > summary:first-of-type) {
  display: list-item;
}

:where(dialog) {
  background-color: white;
  border: solid;
  color: black;
  height: -moz-fit-content;
  height: fit-content;
  left: 0;
  margin: auto;
  padding: 1em;
  position: absolute;
  right: 0;
  width: -moz-fit-content;
  width: fit-content;
}

:where(dialog:not([open])) {
  display: none;
}

/* Misc
   ========================================================================== */
:where(li) {
  list-style-position: outside;
}

:where(h1,
h2,
h3,
h4,
h5,
h6,
blockquote,
figure,
ol,
ul,
li) {
  margin: 0;
  padding: 0;
}

:where(p, pre, hr) {
  margin: 1.125rem 0;
}

:where(blockquote) {
  margin: 1.125rem 2rem;
}

:where(a, button) {
  color: inherit;
}

:where(a) {
  text-decoration: underline;
}

:where(a, a:not(.button):active) {
  background-color: transparent;
}

:where(img, svg) {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
  border: none;
}

:where(svg) {
  overflow: hidden;
  fill: currentColor;
}
:where(svg):root {
  overflow: visible;
}

:where([disabled]) {
  cursor: default;
  opacity: 0.5;
}

:where(fieldset) {
  min-width: 0;
}

:where(body:not(:-moz-handler-blocked) fieldset) {
  display: table-cell;
}

:where(legend) {
  margin-left: -0.3125rem;
  padding: 0 0.3125rem;
  border: none;
}

:where(textarea) {
  vertical-align: top;
  resize: vertical;
}

:where(details, main) {
  display: block;
}

:where(table) {
  margin: 1.125rem 0;
  border-collapse: collapse;
  border-spacing: 0;
}

:where(cite, dfn, em) {
  font-style: italic;
}

:where(iframe) {
  border: none;
}

:where(optgroup) {
  font-weight: 700;
}